<template>
  <div class="main" style="background: #f7f7f7;">
        <iframe :src="meiqia" frameborder="0" class='chat-box'></iframe>
  </div>
</template>
<script>
 import util from '../../../api/util'
export default {
     data() {
            return {
                meiqia:''
            }
        },
        methods: {
           goBack(){
                this.$router.go(-1)
            },
            init(){
                 let _this = this;
                util.request({
                    url: 'member.sendmsg.getmq',
                    method: 'post',
                    data: {},
                    success: function (res) {
                        _this.meiqia = res.data.mq_url
                    }
                })
            }
        }, 
        mounted(){
            this.init();
        },     
}
</script>

<style lang="less" scoped>
    @import '../../../assets/less/index.less';

    .main {
        .page-view(100);
            .chat-box{
                width: 100%;
                height: 100%;
            }
        
    }
</style>


