<template>
    <div class="order">
        <div class="title">
            <div class="left">
                <span class="iconfont">&#xe64a;</span>
                我的订单
            </div>
            <router-link :to="{name:'order',query:{status:''}}"  class="right" tag="div">
                查看全部订单
                <span class="iconfont">&#xe61b;</span>
            </router-link>

        </div>
        <ul class="list">
            <li>
                <router-link :to="{name:'order',query:{status:'0'}}" tag="div">
                    <div class="icon">
                        <div class="icon-nub" v-if="statics.order_0>0">{{statics.order_0}}</div>
                        <span class="iconfont">
                            &#xe686;
                        </span>
                    </div>
                    <span>待付款</span>
                </router-link>
            </li>
            <li>
                <router-link :to="{name:'order',query:{status:'1'}}" tag="div">
                    <div class="icon">
                        <div class="icon-nub" v-if="statics.order_1>0">{{statics.order_1}}</div>
                        <span class="iconfont">
                                &#xe689;
                        </span>
                    </div>
                    <span>待发货</span>
                </router-link>
            </li>
            <li>
                <router-link :to="{name:'order',query:{status:'2'}}" tag="div">

                    <div class="icon">
                        <div class="icon-nub" v-if="statics.order_2>0">{{statics.order_2}}</div>
                        <span class="iconfont">
                            &#xe679;
                        </span>
                    </div>
                    <span>待收货</span>
                </router-link>
            </li>
            <li>
                <router-link :to="{name:'order',query:{status:'4'}}" tag="div">
                    <div class="icon">
                        <div class="icon-nub" v-if="statics.order_4>0">{{statics.order_4}}</div>
                        <span class="iconfont">
                            &#xe80d;
                        </span>
                    </div>
                    <span>退换货</span>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    statics: {
      type: Object,
      required: true
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/less/index.less";
.order {
  width: 100%;
  height: 1.32rem;
  margin-top: 0.1rem;
  background-color: #fff;
  line-height: 1.4;
  font-size: 0.16rem;
  overflow: hidden;
  position: relative;
  border-bottom: 1px solid #ebebec;
  opacity: 0.7;
  .title {
    position: relative;
    padding: 0.125rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
    align-items: center;
    -moz-transition-duration: 300ms;
    transition-duration: 300ms;
    transition-property: background-color;
    text-align: left;
    .left {
      flex: 1;
      color: #333;
      font-size: 0.15rem;
    }
    .right {
      flex: 1;
      text-align: right;
      color: #8a8989;
      font-size: 0.1rem;
      margin-right: 0.04rem;
      .iconfont {
        font-size: 0.1rem;
      }
    }
  }
  ul {
    display: flex;
    position: relative;
    overflow: hidden;
    border-top: 1px solid #e6e6e7;
    background: #fff;
    li {
      flex: 1;
      height: 0.87rem;
      position: relative;
      padding: 0.125rem 0;
      text-align: center;
      /*border-right:0.01rem solid #ebebec;*/
      transition: background-color 300ms;
      -webkit-transition: background-color 300ms;
      .icon {
        height: 0.38rem;
        .icon-nub {
          font-size: 0.12rem;
          padding: 0.02rem 0.06rem;
          background: #ff0000;
          color: #ffffff;
          border-radius: 06rem;
          position: absolute;
          left: 0.58rem;
        }
        span {
          font-size: 0.3rem;
          text-align: center;
        }
      }
      span {
        height: 0.24rem;
        font-size: 0.12rem;
        text-align: center;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        padding: 0.04rem;
        color: #7a7a7a;
        .iconfont {
          font-weight: bold;
        }
      }
    }
  }
}
</style>



